import React from 'react';
import propTypes from 'prop-types';
import { connect } from 'react-redux';
import numeral from 'numeral';
import './amount-items.css';

const TotalLine = ({total}) => {    
    const currencySymbol = '$';

    return ( 
        <div className="amount-items-container">
            <div className="amount-items-total-label">Total</div>
            <div>{currencySymbol}{numeral(total).format('0,0')}</div> 
        </div>
    );
};

TotalLine.propTypes = {
    total: propTypes.number.isRequired
};

const getPriceAdjustments = (items, subtotal) => {
    let totalAdjust = 0;

    if (items && items.length) {
        let percent = 0;
        let amount = 0;

        items.forEach(item =>  {
            percent += item.percent ? item.percent/100 : 0;
            amount += item.amount ? item.amount/100 : 0;
        });

        totalAdjust = amount + subtotal * percent;
    }

    return totalAdjust;
};

const mapStateToProps = (state) => {
    const reservation = state.reservation;

    const days = (reservation.endDate.valueOf() - reservation.startDate.valueOf())/86400000;
    const subtotal = reservation.price * days /100;
    const discounts = getPriceAdjustments(reservation.discounts, subtotal);
    const fees = getPriceAdjustments(reservation.fees, subtotal);

    return {
        total: subtotal + fees - discounts
    };
};

export default connect(mapStateToProps)(TotalLine);