import React from 'react';
import Add from '@material-ui/icons/AddCircleOutline';
import Remove from '@material-ui/icons/RemoveCircleOutline';
import { makeStyles } from '@material-ui/core/styles';
import propTypes from 'prop-types';
import IconButton from '@material-ui/core/IconButton';

const useStyles = makeStyles(() => ({
    container: {
        display: 'flex',
        flexWrap: 'nowrap',
        fontSize: 'large',
        justifyContent: 'space-around',
        alignItems: 'center',
        paddingTop: 5,
        paddingBottom: 5
    },
    guestLabel: {
        fontSize: 'large',
        minWidth: 150
    },
    guestLabelInfo: {
        right: 50,
        top: 15,
        fontSize: 'x-small'
    }
}));

const GuestLine = (props) => {
    const classes = useStyles();
    return (
        <div className={classes.container}>
            <div 
                className={classes.guestLabel}>{ props.label }
                <sub 
                    className={classes.guestLabelInfo}>{props.info}
                </sub>
            </div>
            <IconButton
                aria-label="discount information"
                aria-controls="discount-info"
                aria-haspopup="true"
                className={classes.icons} 
                data-guesttype={props.label}
                onClick={props.decreaseNumberOfGuests}
                color="inherit"
            >
                <Remove fontSize="large" />   
            </IconButton>             
            <div>{ props.value }</div>
            <IconButton
                aria-label="discount information"
                aria-controls="discount-info"
                aria-haspopup="true"
                data-guesttype={props.label}
                onClick={props.increaseNumberOfGuests}
                color="inherit"
            >
                <Add fontSize="large" />
            </IconButton>
        </div>
    );
};

GuestLine.propTypes = {
    label: propTypes.string.isRequired,
    value: propTypes.number.isRequired,
    info: propTypes.string,
    increaseNumberOfGuests: propTypes.func.isRequired,
    decreaseNumberOfGuests: propTypes.func.isRequired
};

export default GuestLine;