import React from 'react';
import { DateRangePicker } from 'react-dates';
import {setStartDate, setEndDate} from '../../../store/reservationActions';
import propTypes from 'prop-types';
import { connect } from 'react-redux';
import 'react-dates/lib/css/_datepicker.css';
import * as icons from '../../icons/icons';
import './DatesInput.css';

const DatesInput = (props) => {
    const [focusedInput, setFocusInput] = React.useState(null);

    const onDateChange = ({ startDate, endDate }) => {
        props.dispatch(setStartDate(startDate));
        props.dispatch(setEndDate(endDate));
    };

    const onFocusChange = (focusedInput) => setFocusInput(focusedInput);

    const isDayBlocked  = (day) => {
        // eslint-disable-next-line no-unused-vars
        for (const reservation of props.blockedDays) {
            if (day.valueOf() >= reservation.startDate.valueOf() && day.valueOf() <= reservation.endDate.valueOf()) {
                return true;
            }
        }
        return false;
    };

    return (
        <div>
            Dates<br/>
            <DateRangePicker
                startDate= {props.startDate}
                endDate= {props.endDate}
                onDatesChange={onDateChange}
                focusedInput={focusedInput}
                onFocusChange={onFocusChange}
                numberOfMonths={1}
                showClearDates={true}
                startDateId="startDate"
                endDateId="endDate"
                startDatePlaceholderText="Check-in"
                endDatePlaceholderText="Checkout"
                isDayBlocked={isDayBlocked}
                customArrowIcon = { <icons.RightArrow /> }
            />
        </div> 
    );    
};

DatesInput.propTypes = {
    dispatch: propTypes.func.isRequired,
    startDate: propTypes.object,
    endDate: propTypes.object,
    blockedDays: propTypes.array
};

const mapStateToProps = (state) => {
    const reservation = state.reservation;
    return {
        startDate: reservation.startDate,
        endDate: reservation.endDate,
        blockedDays: reservation.blockedDays
    };
};

export default connect(mapStateToProps)(DatesInput);

