import moment from 'moment';
import defaultState from './reservationDefaultState';

export default (state = defaultState, action) => {
    switch(action.type) {
        case 'SET_START_DATE':
            return {...state, 'startDate': action.startDate};
        case 'SET_END_DATE':
            return isBlockedRange({ startDate: state.startDate, endDate: action.endDate, blockedDays: state.blockedDays})
                ? {...state, 'endDate': null}
                : {...state, 'endDate': action.endDate};
        case 'SET_GUESTS':
            return {...state, guests: action.guests};
        default:
            return state;
    }
};

const isBlockedRange = ({startDate, endDate, blockedDays}) => {

    if (startDate && endDate) {
        const iterate = moment(startDate);
        while (iterate.valueOf() <= endDate.valueOf()) {
            if (isDayBlocked(iterate, blockedDays)) return true;
            iterate.add(1, 'days');
        }    
    }
    return false;
};

const isDayBlocked  = (day, blockedDays) => {
    // eslint-disable-next-line no-unused-vars
    for (const reservation of blockedDays) {
        if (day.valueOf() >= reservation.startDate.valueOf() && day.valueOf() <= reservation.endDate.valueOf()) {
            return true;
        }
    }
    return false;
};
