import React from 'react';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
import propTypes from 'prop-types';
import numeral from 'numeral';

const useStyles = makeStyles(() => ({
    currency: {
        fontSize: 20,
        fontWeight: 800
    },
    divider: {
        marginTop: 10,
        marginBottom: 15
    }
}));

const PriceLabel = (props) => {
    const classes = useStyles();
    const currencySymbol = '$';
    const priceLabel = `${currencySymbol}${numeral(props.price/100).format('0,0')}`;

    return ( 
        <div>
            <span className={classes.currency}>{priceLabel}</span> per night
            <Divider className={classes.divider} />
        </div> 
    );
};

PriceLabel.propTypes = {
    price: propTypes.number.isRequired
};

const mapStateToProps = (state) => {
    return {
        price: state.reservation.price
    };
};
 
export default connect(mapStateToProps)(PriceLabel);
