/* eslint-disable react/display-name */
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import HomeIcon from '@material-ui/icons/AccountBalanceTwoTone';
import AccountCircle from '@material-ui/icons/AccountCircle';
import propTypes from 'prop-types';
import { Link } from 'react-router-dom';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Drawer from '@material-ui/core/Drawer';
import './Navigation.css';
import { connect } from 'react-redux';

const useStyles = makeStyles(() => ({
    root: {
        flexGrow: 1
    },
    list: {
        width: 180,
    },
    appTitle: {
        fontSize: 'small',
        color: 'yellow',
        position: 'relative',
        right: 42,
        top: 22
    },
    items: {
        padding: 15,
        flexGrow: 1
    },
    greeting: {
        fontSize: 'large',
        backgroundColor: '#3f51b5',
        padding: 18,
        color: '#fff',
        textAlign: 'right'
    }
}));


const Navigation = (props) => {
    const classes = useStyles();
    const [state, setState] = React.useState({right: false});
    const [currentPage, setNavigatedToPage] = React.useState('');

    const getLinks = () => (
        props.isAuthenticated
            ?  [{
                name: 'Manage Property',
                to: '/property'
            }, {
                name:  'Make Reservation',
                to: '/reservation'
            },{
                name: 'Logout',
                to: '/logout'
            }]
            : [{
                name: 'Sign in',
                to: '/login'
            }]
    ); 

    const toggleDrawer = (open) => event => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
    
        setState({ right: open });
    };

    const setCurrentPage = (page) => {
        (page !== 'Home' && page !== 'Sign in' && page !== 'Logout')
            ? setNavigatedToPage(page)
            : setNavigatedToPage('');
        return null;
    };

    const sideList = () => (
        <div
            className={classes.list}
            role="presentation"
            onClick={toggleDrawer(false)}
            onKeyDown={toggleDrawer(false)}
        >
            <div className={classes.greeting}>{
                props.isAuthenticated 
                    ? 
                    `Hello, ${props.firstName}`
                    :
                    'Welcome to YoCasa'
            }
            </div>
            <Divider />
            <List>
                {getLinks().map((link) => (
                    <ListItem button key={link.name}>
                        <Link onClick={setCurrentPage.bind(this, link.name)} className="nostyle" to={link.to}><ListItemText primary={link.name} /></Link>
                    </ListItem>)
                )}
            </List>
        </div>
    );
    
    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Toolbar disableGutters>
                    <Link onClick={setCurrentPage.bind(this, 'Home')} to="/">
                        <IconButton color="inherit" aria-label="home">
                            <HomeIcon color="secondary"
                                fontSize="large" />
                            <div className={classes.appTitle}>YoCasa</div>
                        </IconButton>
                    </Link>
                    <div className={classes.items}>{currentPage}</div>
                    { (
                        <div>
                            <IconButton
                                aria-label="account of current user"
                                aria-controls="menu-appbar"
                                aria-haspopup="true"
                                color="inherit"
                                onClick={toggleDrawer('right', true)}
                            >
                                {props.isAuthenticated ? <MenuIcon /> : <AccountCircle />}
                            </IconButton>
                            <Drawer anchor="right" open={state.right} onClose={toggleDrawer(false)}>
                                {sideList()}
                            </Drawer>
                        </div>
                    )}
                </Toolbar>
            </AppBar>
        </div>
    );
};

Navigation.propTypes = {
    isAuthenticated: propTypes.bool.isRequired,
    firstName: propTypes.string
};

const mapStateToProps = (state) => {
    const user = state.user;
    return {
        isAuthenticated: user.isAuthenticated,
        firstName: user.firstName
    };
};

export default connect(mapStateToProps)(Navigation);