import React from 'react';
import propTypes from 'prop-types';
import { connect } from 'react-redux';
import numeral from 'numeral';
import Divider from '@material-ui/core/Divider';
import './amount-items.css';
import Info from '@material-ui/icons/InfoOutlined';
import Popover from '@material-ui/core/Popover';
import IconButton from '@material-ui/core/IconButton';

const DiscountInformation = (info) => {
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const open = Boolean(anchorEl);

    const id = open ? 'simple-popover' : undefined;

    return (
        <span>
            <IconButton
                aria-label="discount information"
                aria-controls="discount-info"
                aria-haspopup="true"
                onClick={handleClick}
                color="inherit"
                aria-describedby={id}
                className="amount-items-info-icon"
            >
                <Info 
                    fontSize="small" 
                    aria-describedby={id}
                />
            </IconButton>
            <Popover
                id={id}
                open={open}
                anchorEl={anchorEl}
                onClose={handleClose}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                }}
                transformOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                }}
            >
                <span>No Info</span>
            </Popover>
        </span>
    );
};


const displayItem = (item, subtotal, discount) => {

    const currencySymbol = '$';
    
    const amount = item.percent ? numeral(subtotal*item.percent/100).format('0,0') :
        item.amount ? numeral(item.amount/100).format('0,0') : 0;
    
    return amount ?
        (
            <div>
                <div className="amount-items-container">
                    <div>
                        {item.percent ? `${item.percent}% ${item.description}` :  `${item.description}`}
                        {item.info && <DiscountInformation info={item.info} />}
                    </div>
                    <div>{discount && '-'} {currencySymbol}{amount}</div> 
                </div>
                <Divider className="amount-items-divider" />
            </div>
        ) : null;
};

const getItems = (items, subtotal, discount) => (
    items && items.length && items.map((item) => (
        <div key={item.id}>
            {displayItem(item, subtotal, discount)} 
        </div>
    ))
);

const DiscountAndFeesLabels = ({discounts, fees, subtotal}) => (
    <div> 
        {getItems(discounts, subtotal, true)}
        {getItems(fees, subtotal)}
    </div> 
);    



DiscountAndFeesLabels.propTypes = {
    discounts: propTypes.array.isRequired,
    fees: propTypes.array.isRequired,
    subtotal: propTypes.number.isRequired
};
 
const mapStateToProps = (state) => {
    const reservation = state.reservation;

    const days = (reservation.endDate.valueOf() - reservation.startDate.valueOf())/86400000;
    const subtotal = reservation.price * days /100;

    return {
        discounts: reservation.discounts,
        fees: reservation.fees,
        subtotal: subtotal
    };
};

export default connect(mapStateToProps)(DiscountAndFeesLabels);