import React from 'react';
import DatesInput from './components/DatesInput';
import GuestInput from './components/GuestInput';
import PriceLabel from './components/PriceLabel';
import Subtotal from './components/Subtotal';
import DiscountAndFeesLabels from './components/DiscountAndFeesLabels';
import TotalLine from './components/TotalLine';
import Button from '@material-ui/core/Button';
import { connect } from 'react-redux';
import propTypes from 'prop-types';
import './reservationForm.css';

const Amounts = (props) => {
    if (props.startDate && props.endDate && props.price) 
        return (
            <div className="reservation-amounts-container">
                <Subtotal />  
                <DiscountAndFeesLabels /> 
                <TotalLine />
            </div>
        );
    else return null;
};

const ReservationForm = (props) => (
    <div className="reservation-container">
        <PriceLabel />
        <DatesInput />
        <GuestInput />
        <Amounts {...props} /> 
        <Button className="reservation-reserve-button" disabled={!props.startDate || !props.endDate || !props.price} variant="contained" color="secondary" >
            Reserve
        </Button>
    </div>
);

ReservationForm.propTypes = {
    startDate: propTypes.object,
    endDate: propTypes.object,
    price: propTypes.number
};

const mapStateToProps = (state) => {
    const reservation = state.reservation;

    return {
        startDate: reservation.startDate,
        endDate: reservation.endDate,
        price: reservation.price
    };
};
 
export default connect(mapStateToProps)(ReservationForm);
