export default (state = {
    isAuthenticated: false,
}, action) => {
    switch(action.type) {
        case 'GET_CURRENT_USER_STATUS':
            return state;
        case 'USER_SIGN_IN':
            return {...state, 
                isAuthenticated: true, 
                firstName: 'Luis', 
                lastName: 'Fortes', email: 'lmlf100@gmail.com'
            };
        case 'USER_SIGN_OUT':
            return {...state, isAuthenticated: false};
        default:
            return state;
    }
};