import React from 'react';
import { connect } from 'react-redux';
import Divider from '@material-ui/core/Divider';
import propTypes from 'prop-types';
import numeral from 'numeral';
import './amount-items.css';

const Subtotal = ({ startDate, endDate, price }) => {

    const currencySymbol = '$';
    const days = (endDate.valueOf() - startDate.valueOf())/86400000;
    const subtotalLabel = `${currencySymbol}${numeral(price * days /100).format('0,0')}`;
    const priceLabel = `${currencySymbol}${numeral(price/100).format('0,0')}`;

    const nightLabel = () => {
        return days === 1 ? '1 night' : `${days} nights`;
    };

    return ( 
        <div>
            <div className="amount-items-container">
                <div>{`${priceLabel} X ${nightLabel()}`}</div>
                <div>{subtotalLabel}</div> 
            </div>
            <Divider className="amount-items-divider" />
        </div>
    );
};

Subtotal.propTypes = {
    startDate: propTypes.object.isRequired,
    endDate: propTypes.object.isRequired,
    price: propTypes.number.isRequired
};

const mapStateToProps = (state) => {
    const reservation = state.reservation;

    return {
        startDate: reservation.startDate,
        endDate: reservation.endDate,
        price: reservation.price
    };
};
 
export default connect(mapStateToProps)(Subtotal);
